import {Audit} from '@joh-5a02ea7d-205c-4fe1-b4f3-a1f20ed82559/libs';
import {Injectable} from '@nestjs/common';
import {InjectRepository} from '@nestjs/typeorm';
import {Repository} from 'typeorm';

@Injectable()
export class AuditService {
  constructor(
    @InjectRepository(Audit)
    private auditRepository: Repository<Audit>
  ) {}

  async findAll(): Promise<Audit[]> {
    return this.auditRepository.find();
  }

  async findOne(id: number): Promise<Audit> {
    return this.auditRepository.findOne({where: {id}});
  }

  async create(Audit: Partial<Audit>): Promise<Audit> {
    const newAudit = this.auditRepository.create(Audit);
    return this.auditRepository.save(newAudit);
  }

  async update(id: number, Audit: Partial<Audit>): Promise<Audit> {
    await this.auditRepository.update(id, Audit);
    return this.auditRepository.findOne({where: {id}});
  }

  async delete(id: number): Promise<void> {
    await this.auditRepository.delete(id);
  }
}
